.container{
  padding-top: 100px;
  margin: 0 20px 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas:
  "dis dis dis dis"
  "AC C div times"
  "seven eight nine minus"
  "four five six plus"
  "one two three equal"
  "zero zero dot equal"
}

.padButton{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 40px;
  background-color: gray;
  color: white;
  border: 1px solid black;
  cursor: pointer;
  font-size: 20px;
}
.padButton:hover{
  border: 1px solid white;
  color: #ccc;
}
.dark-gray{
  background-color: #333;
}
.tomato{
  background-color: tomato;
}
.blue{
  background-color:#5599ff;
}
.dis{
  grid-area: dis;
  background-color: black;
  text-align: right;
  font: 20px;
  height: 60px;
}
.dis input{
  width: 88%;
  color: white;
  padding: 5px 20px;
  text-align: right;
  background-color: black;
}
input::placeholder{
  color: white;
}
.total{
  padding: 5px 20px;
  color: yellow;
}
.zero{
  grid-area: zero;
}

.equal{
  grid-area: equal;
}
